import './App.css';
import React, {KeyboardEvent, useState} from "react";
import {v4} from "uuid";
import styles from "./App.module.css"
import {SearchBar} from "./components/SearchBar/SearchBar.tsx";
import {SearchResults} from "./components/SearchResults/SearchResults.tsx";
import {Playlist} from "./components/Playlist/Playlist.tsx";
import {Button} from "@mui/material";
import {CLIENT_ID, getToken, REDIRECT_URI, refreshToken, SpotifyApi} from "./api/spotify-api.ts";


const App = () => {
    let [state, setState] = useState({})
    let [tracks, setTracks] = useState({
        items: [{name: "Diamonds", id: v4(), artists: [{name: "Rihanna"}]}, {
            name: "Diam",
            id: v4(),
            artists: [{name: "Hanna"}]
        }]
    })
    let [loading, setLoading] = useState(false);
    let [playlist, setPlaylist] = useState([]);
    const SCOPES = 'user-read-private user-read-email playlist-modify-public playlist-modify-private playlist-read-private playlist-read-collaborative'; // Add scopes as needed
    const AUTH_URL = `https://accounts.spotify.com/authorize?response_type=code&client_id=${CLIENT_ID}&redirect_uri=${encodeURIComponent(REDIRECT_URI)}&scope=${encodeURIComponent(SCOPES)}`;
    let [playlistTitle, setPlaylistTitle] = useState('');
    const [userId, setUserId] = useState('');

    const initAuth = () => {
        getToken()
    }


    const updateData = (res) => {
        setTracks(res.data.tracks)
        setLoading(false)
    }

    const setData = (query) => {
        setLoading(true)
        setTracks({items: []})
        SpotifyApi.getTracks(query)
            .then((res) => {
                updateData(res)
            })
            .catch(error => {
                if (error.status === 401 || error.status === 400) {
                    refreshToken()
                    SpotifyApi.getTracks(query)
                        .then((res) => {
                            updateData(res)
                        })
                }
            })
    };

    const setKeypressData = (e: KeyboardEvent<HTMLDivElement>, query) => {
        if (e.key === "Enter") {
            setData(query)
        }
    }

    const moveToPrevious = () => {
        SpotifyApi.moveThroughList(tracks.previous)
            .then(res => {
                setTracks(res.data.tracks)
            })
    }

    const moveToNext = () => {
        console.log(tracks.next)
        SpotifyApi.moveThroughList(tracks.next)
            .then(res => {
                setTracks(res.data.tracks)
            })
    }

    const checkDuplicates = (playlist, idx) => {
        return playlist.find(item => item.id === tracks.items[idx].id)
    }


    const updatePlaylist = (idx) => {
        if (checkDuplicates(playlist, idx)) {
            alert("Sorry, Your playlist already have this track")
        } else {
            setPlaylist([tracks.items[idx], ...playlist])
        }
    }

    const removeItemFromPlaylist = (idx) => {
        setPlaylist(playlist.filter((item, index) => index !== idx))
    }


    const createPlaylist = (title) => {
        const uris = playlist.map(el => el.uri)

        SpotifyApi.me()
            .then(res => {
                setUserId(res.data.id)
                console.log(userId)
                SpotifyApi.createPlaylist(title, userId)
                    .then(res => {
                        SpotifyApi.addItemsTracks(res.data.id, uris)
                            .then(res => {
                                alert(`Playlist ${title} Successfuly created`)
                                SpotifyApi.getUsersPlaylists(userId)
                                    .then(res => {
                                        console.log(res)
                                    })
                            })
                    })
            })

    }


    return (
        <div className="App">
            <h1>Jammming</h1>

            <Button variant={"outlined"} style={{fontWeight: "bold", border: "3px solid"}} color={"success"}
                    href={AUTH_URL} onClick={initAuth}>Authorize SPOTIFY</Button>
            <SearchBar getQuery={setData} onButtonPress={setKeypressData}/>
            <div className={styles.main}>
                <div>
                    <SearchResults title={"Results"} tracks={tracks} loading={loading} addToPlaylist={updatePlaylist}/>
                    <div className={styles.pagination}>
                        <Button onClick={moveToPrevious} disabled={tracks.previous === null}>{"<"}</Button>
                        <span>{tracks.offset ? Math.ceil(tracks.offset / 20) + 1 : 1}</span>
                        <Button onClick={moveToNext} disabled={tracks.next === null}>{">"}</Button>
                    </div>
                </div>
                <div>
                    {playlist.length > 0 &&
                    <Playlist title={playlistTitle} tracks={playlist} removeItem={removeItemFromPlaylist}
                              createPlaylist={createPlaylist}/>}
                </div>
            </div>
        </div>
    );
}

export default App;
